@import"../../style/base.scss";

.background {
  z-index: 9999; //浮起來
  width: 100%;
  height: 100%;
  background-color: rgba(51, 54, 56, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
}

.container {
  width: 50%;
  height: 300px;
  position: fixed;
  top: 56px;
  left: 50%;
  transform: translateX(-50%);
  background-color: $background-color;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 14px;
}

.isLoading {
  filter: contrast(.6),
}

.buttonContainer {
  width: 100%;
  height: 56px;
  text-align: left;
  padding: 12px 20px;
  border-bottom: 1px solid #E6ECF0;

  button {
    width: 30px;
  }
}

.backButton {
  display: none;
}

.closeButton {
  display: block;
}



.ContentContainer {
  display: flex;
  width: 100%;
  padding: 16px 16px 0 24px;
}


.avatarContainer {
  @include box(50, 50);
  object-fit: cover;
  border: 1px solid $background-color;

  >img {
    border-radius: 50%;
  }

}

.textareaContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;

}

.small {
  @include fontStyle(15, 400);
  padding: 12px;
  color: $error-color;
}

// 父親是ContentContainer，
textarea {
  height: 100%;
  border: none;
  text-align: start;
  margin-top: 0.5rem;
  margin-left: 1rem;
  padding: unset;
}


.button {
  position: relative;
  margin-top: 10rem; //不確定可不可以寫死
  @include box(64, 40);
  @include fontStyle(16, 400);
  background: $brand-color;
  border-radius: 50px;
  line-height: 24px;
  color: $background-color;
}

// 手機
@media screen and (max-width: 426px) {
  .container {
    width: 100%;
    height: 100vh;
    top: 0px;
    border-radius: 0;
  }

  .button {
    position: fixed;
    margin: 9px 0;
    top: 0;
    right: 20px;
  }

  .ContentContainer {
    height: calc(100vh - 120px);
  }

  .backButton {
    display: block;
  }

  .closeButton {
    display: none;
  }
}