@import '../../style/base.scss';

.followCardContainer {
    position: relative;
    //撐開
    width: 100%;

    img {
        width: 50px;
        height: 50px;

    }

    border: 1px solid $border-color;

}

.followCard {
    display: flex;
    align-items: flex-start;
    height: 143px;
    box-sizing: border-box;

}



.avatar {
    margin-top: 16px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 16px;
}

.intro {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    //justify-content: center;
    flex-grow: 1;

}

.userInfo {
    margin-top: 5px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-grow: 1;
    margin-left: 2rem;

}



.name {
    width: auto; // 讓名稱的寬度可以自適應
    height: 26px;
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    color: #171725;
    margin-top: 10px;
}

.userName {
    width: auto; // 讓用戶名的寬度可以自適應
    height: 22px;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    color: #92929D;
    margin-top: 10px;
}

// buttonFollower取消flex
.buttonFollowing,
.buttonFollower {
    @include fontStyle(16, 400);
    margin-top: 10px;
    align-items: center;
    text-align: center;
    height: 40px;
    margin-left: 16px;
    line-height: 24px;
    border-radius: 50px;
    top: calc(50% - 40px/2); //figma:直接除二
    margin-right: 16px;

}

.buttonFollowing {
    display: flex;
    width: 80px;
    font-style: normal;
    color: $background-color;
    flex: none;
    background: $brand-color;

}

.buttonFollower {
    width: 64px;
    background: $background-color;
    color: $brand-color;
    border: 1px solid $brand-color;
    white-space: nowrap;
}

.intro {
    margin-top: 8px;
}